.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2em;
}
.chosenBox {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    width: 70%;
    height: 15em;
    overflow: scroll;
    margin: 1em 0;
}
.btnBox {
    display: flex;
    flex-direction: row;
    margin: 0 .5em 1em .5em;
}
.colorItem {
    display: flex;
    flex-direction: row;
    margin: .5em;
}
.instructions {
    display: flex;
    justify-content: center;
    width: 70%;
    height: 15em;
    overflow: scroll;
    flex-direction: row;
}
.header {
    font-family: 'Monoton', cursive;
    font-size: 3em;
}
.active {
    padding: .5em;
    font-size: 1em;
    background-color: #04b3d1;
    color: #fff;
    border-radius: 5px;
    border: #04b3d1;
    margin: .5em;
}
.inactive {
    padding: .5em;
    font-size: 1em;
    background-color: #fff;
    color: #04b3d1;
    border-radius: 5px;
    border: #fff;
    margin: .5em;
}
.swapBtn {
    font-size: .8em;
    background-color: #fff;
    color: #04b3d1;
    border-radius: 5px;
    border: #fff;
    margin: auto .5em;
}

@media (max-width: 967.98px) {
    .instructions { 
        width: 100%;
    }
    .chosenBox {
        display: grid;
        grid-template-columns: auto auto auto;
        width: 100%;
    }
}